.cardContainer {
    display: inline-block;
    position: relative;
    border-radius: 20px;
    overflow: hidden;
    width: 100%;
    cursor: pointer;
    transition: 1s;
    &:hover {
        box-shadow: 0px 5px 5px #9e9e9e;
    }
}

.filter {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(
        180deg,
        rgba(255, 255, 255, 0) 0%,
        rgba(0, 0, 0, 0.5) 100%
    );
    border-radius: 10px;
}

.imageCard {
    height: 100%;
}

.accommodationTitle {
    position: absolute;
    bottom: 5%;
    left: 0;
    color: #fff;
    width: 100%;
    padding: 5px;
    font-size: 16px;
    text-align: left;
    width: 280px;
}
